import React from 'react';
import primaryPhoto from '../primaryPhoto.svg'
import name from '../name.svg'
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';

const IndexPhoto = props => {
    return (
        <Container fluid className="primary">
            <Row>
                <Col xs={12} md={6} className="first-col">
                    <div>
                        <img className='namePhoto' src={name}></img>

                    </div>
                </Col>
                <Col xs={12} md={6} className="first-col">
                    <div className="primary-div">
                        <img id="primaryPhoto" className='primaryPhoto' src={primaryPhoto}></img>

                    </div>
                </Col>



            </Row>
        </Container>
    )
}
export default IndexPhoto;