const Aboutme = props => {
    return (
        <Container className="second">
            <Row className="justify-content-md-center h-100" xs={1} sm={2}>
                <Col xs={12} md={6} className="second-col">
                    <div className="secondPhoto-div">
                        <img className='secondPhoto' src={secondPhoto}></img>
                        <div className="secondPhotoDeco"></div>
                    </div>


                </Col >
                {/* <div class="clearfix visible-xs"></div> */}
                <Col xs={12} md={6} className="second-col">
                    <div className="aboutMe-content">
                        <h2>About me</h2>
                        <h3>designer/coder</h3>
                        <p>
                            目就讀國立政治大學資訊管理學系，<br></br>
                    雙主修數位內容與科技學士學位學程。<br></br>
                            <br></br>

                        喜歡學習新東西，<br></br>
                    享受不同技術帶來的新奇；<br></br>
                    喜歡美麗的事物，<br></br>
                    也熱愛將事物變得更美好；<br></br>
                    目前努力學習web 程式設計。<br></br></p>
                    </div>


                </Col>
            </Row>


        </Container>
    )
}
export default Aboutme;